import { isType } from "@tomato-js/shared";
/**
 * Node
 *
 *
 * 
 * ```javascript
 *   import { isNode } from '@tomato-js/env'
 *   isNode();//false
 * ```
 *
 * @returns global
 */
export const isNode = () => typeof process !== "undefined" && isType(process, "process");
